import React, { useState, useEffect, Component } from 'react';
import './Paint.css';
import { create, all } from 'mathjs';
import interact from 'interactjs'; 
import Atrament from 'atrament';


function Paint(props){

  function Dibujo(){

      useEffect(() => {
      
          const position = { x: 0, y: 0 }

          interact('.draggable1')
          .draggable({
            listeners: {
              start (event) {
                console.log(event.type, event.target)
              },
              move (event) {
                position.x += event.dx
                position.y += event.dy

                event.target.style.transform =
                  `translate(${position.x}px, ${position.y}px)`
              },
            },
          })
          .ignoreFrom('#sketchpad')

          const canvas = document.getElementById('sketchpad');


          const sketchpad = new Atrament(canvas, {
            width: 395,
            height: 345,
            color: 'black',
          });

          sketchpad.smoothing = 1.3;

      }, []);
        return (

              <div className="Paint contenedorPrograma draggable1 scale-up-center">
                      
                      <div class="card">
                        <div class="card-header">
                          Paint :)
                          <button onClick={props.cerrarPaint} type="button" class="btn btn-danger">X</button>
                        </div>
                        <div class="card-body">
                            <canvas id="sketchpad" width="395" height="345"></canvas>  
                        </div>
                      </div>
                      
              </div>
        );

  }


    let abierto = props.abierto


    if(abierto){

        return (<Dibujo/>);

      }else{
        return(<span></span>);
      }

}

export default Paint;
