import React, { useState, useEffect } from 'react';

import './App.css';

import logo from'./img/logo1.png';

import calculadoraImg from'./img/calculadora1.png';

import anotadorImg from'./img/anotador1.png';

import paintImg from'./img/paint1.png';

import github from'./img/github2.png';

import interact from 'interactjs'; 

import Atrament from 'atrament';

import { create, all } from 'mathjs';

import Calculadora from './Calculadora/Calculadora';

import Paint from './Paint/Paint';

import Anotador from './Anotador/Anotador';

function App() {

  const [calculadora, setCalculadora] = useState(false)

  const [paint, setPaint] = useState(false)

  const [anotador, setAnotador] = useState(false)

  function handleClickCalculadora(e){

      setCalculadora(true)

  }

  function handleClickPaint(e){

      setPaint(true)

  }

  function handleClickAnotador(e){

      setAnotador(true)

  }

  function cerrarPaint(e){

      setPaint(false)

  }

  function cerrarAnotador(e){

      setAnotador(false)

  }

  function cerrarCalculadora(e){

      setCalculadora(false)

  }

  return (

        <div class="App">

          <Paint abierto={paint} cerrarPaint={cerrarPaint} />


          <Anotador abierto={anotador} cerrarAnotador={cerrarAnotador} />


          <Calculadora abierto={calculadora}  cerrarCalculadora={cerrarCalculadora} />


          
          <div class="pantalla">


            <a href="https://github.com/juanignacioluna" target="_blank">
              <div class="programa">

                <img src={github}></img>

                <h6>Github</h6>
                
              </div>
            </a>
            


            <div id="paint" class="programa" onClick={handleClickPaint}>

              <img src={paintImg}></img>

              <h6>Paint</h6>
              
            </div>

            <div id="calculadora" class="programa" onClick={handleClickCalculadora}>

              <img src={calculadoraImg}></img>

              <h6>Calculadora</h6>
              
            </div>

            <div id="anotador" class="programa" onClick={handleClickAnotador}>

              <img src={anotadorImg}></img>

              <h6>Anotador</h6>
              
            </div>
            
          </div>



          <div class="barra">

            <div class="icono">
              <img src={logo}></img>
            </div>

            <div class="icono">
              <img src={calculadoraImg}></img>
            </div>

            <div class="icono">
              <img src={anotadorImg}></img>
            </div>

            <div class="icono">
              <img src={paintImg}></img>
            </div>
            
          </div>

        </div>
  );
}

export default App;
