import React, { useState, useEffect, Component } from 'react';
import './Calculadora.css';
import { create, all } from 'mathjs';
import interact from 'interactjs'; 

function Calculadora(props){

  const [resultado, setResultado] = useState("0")


  useEffect(() => {


      const position3 = { x: 0, y: 0 }

      interact('.draggable3')
      .draggable({
        listeners: {
          start (event) {
            console.log(event.type, event.target)
          },
          move (event) {
            position3.x += event.dx
            position3.y += event.dy

            event.target.style.transform =
              `translate(${position3.x}px, ${position3.y}px)`
          },
        },
      })
  

  }, []);


  function handleBotonCalculadora(e){

    let valor = e.target.innerHTML

    const config = { }

    const math = create(all, config)

    switch (valor) {

      case 'C':
        setResultado("0")
        break;

      case '=':{

        if(!(resultado=="Syntax ERROR")){

          try {

            setResultado(math.evaluate(resultado).toFixed(2));

          }catch (error) {

            setResultado("Syntax ERROR")

          }

        }
        
        break;

      }


      case '/':
      case '*':
      case '-':
      case '+':
      case '9':
      case '8':
      case '7':
      case '6':
      case '5':
      case '4':
      case '3':
      case '2':
      case '1':
      case '0':
        
        if(resultado=="0"){

          setResultado(valor)
        
        }else if(resultado=="Syntax ERROR"){

        }else{
          setResultado(resultado+valor)
        }
        

        break;
    }

  }


    let abierto = props.abierto


    if(abierto){

        return (

              <div className="Calculadora contenedorPrograma draggable3 scale-up-center">


                  <div class="card">
                    <div class="card-header">
                      Calculadora
                          <button type="button" class="btn btn-danger" onClick={props.cerrarCalculadora}>X</button>
                    </div>
                    <div class="card-body">


                    <div class="calculadora">


                        <div class="resultado">
                          <h1>{resultado}</h1>
                        </div>

                        <button type="button" class="btn btn-light ce" 
                        onClick={handleBotonCalculadora}>C</button>

                          
                        <button type="button" class="dividir btn btn-light" 
                        onClick={handleBotonCalculadora}>/</button>

                          
                        <button type="button" class="multiplicar btn btn-light" 
                        onClick={handleBotonCalculadora}>*</button>

                          
                        <button type="button" class="restar btn btn-light" 
                        onClick={handleBotonCalculadora}>-</button>

                          
                        <button type="button" class="sumar btn btn-light" 
                        onClick={handleBotonCalculadora}>+</button>

                          
                        <button type="button" class="igual btn btn-light" 
                        onClick={handleBotonCalculadora}>=</button>

                          
                        <button type="button" class="cero btn btn-light" 
                        onClick={handleBotonCalculadora}>0</button>

                          
                        <button type="button" class="uno btn btn-light" 
                        onClick={handleBotonCalculadora}>1</button>

                          
                        <button type="button" class="dos btn btn-light" 
                        onClick={handleBotonCalculadora}>2</button>

                          
                        <button type="button" class="tres btn btn-light" 
                        onClick={handleBotonCalculadora}>3</button>

                          
                        <button type="button" class="cuatro btn btn-light" 
                        onClick={handleBotonCalculadora}>4</button>
                        
                          
                        <button type="button" class="cinco btn btn-light" 
                        onClick={handleBotonCalculadora}>5</button>
                        
                          
                        <button type="button" class="seis btn btn-light" 
                        onClick={handleBotonCalculadora}>6</button>
                        
                          
                        <button type="button" class="siete btn btn-light" 
                        onClick={handleBotonCalculadora}>7</button>
                        
                          
                        <button type="button" class="ocho btn btn-light" 
                        onClick={handleBotonCalculadora}>8</button>
                        
                          
                        <button type="button" class="nueve btn btn-light" 
                        onClick={handleBotonCalculadora}>9</button>


                      </div>

                    </div>
                  </div>

              </div>
        );

      }else{
        return(<span></span>);
      }

}

export default Calculadora;
