import React, { useState, useEffect, Component } from 'react';
import './Anotador.css';
import { create, all } from 'mathjs';
import interact from 'interactjs'; 
import Atrament from 'atrament';


function Anotador(props){


  useEffect(() => {

      const position2 = { x: 0, y: 0 }

      interact('.draggable2')
      .draggable({
        listeners: {
          start (event) {
            console.log(event.type, event.target)
          },
          move (event) {
            position2.x += event.dx
            position2.y += event.dy

            event.target.style.transform =
              `translate(${position2.x}px, ${position2.y}px)`
          },
        },
      })
      .ignoreFrom('textarea')

        
  }, []);

    let abierto = props.abierto


    if(abierto){

        return (

          <div className="Anotador contenedorPrograma draggable2 scale-up-center">


              <div class="card">
                <div class="card-header">
                  Anotador :)
                  <button type="button" class="btn btn-danger" onClick={props.cerrarAnotador}>X</button>
                </div>
                <div class="card-body">

                    <textarea id="output" name="output" rows="9" wrap="virtual"
                    cols="48"></textarea>


                </div>
              </div>

          </div>

          );

      }else{
        return(<span></span>);
      }

}

export default Anotador;
